.header {
  position: relative;
}

.header img {
  width: 100%;
  height: 412px;
}

.header h1 {
  position: absolute;
  height: 80px;
  left: 9.17%;
  right: 69.03%;
  top: calc(50% - 80px / 2 + 118px);

  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: bold;
  font-size: 64px;
  line-height: 80px;

  color: #ffffff;
}
